import * as React from 'react';
import { useState } from 'react';
import { Card, Stack, Button } from '@mui/joy';
import { Typography, TextField } from '@mui/material';
import axios from 'axios';

function Playground() {

    function shuffle(arr) {
        const arru = [arr.substring(2, 7), arr.substring(10,15), arr.substring(18,23), arr.substring(26,31), arr.substring(34,39)]; // turn array into string... what am i implying here? if we wanted to generalize this what would we do?
        let inp_len = arru.length;
        let out_len = inp_len - 2;
        let index = Math.floor(inp_len / 2);
        let ans = new Array(out_len).fill('');

        arru.forEach(elm => {
            if (elm.length !== inp_len) {
                throw new Error('Assertion failed: element length should match input length');
            }
        });
    
        for (let i = 0; i < inp_len; i++) {
            if ((i !== index - 1) && (i !== index + 1)) {
                ans[index - 1] += arru[i].charAt(index);
                for (let j = 0; j < index - 1; j++) { // Maybe the number of times we go through this loop is important somehow to the final answer... Just maybe lol... And maybe it has to do with when we create some other part of the final ans.... Maybe...
                    ans[j] += arru[i].charAt(inp_len - 1 - j); // to be clear you can wonder about the amount of these executions...
                    ans[out_len - j - 1] += arru[i].charAt(j);
                }
            }
        }

        return `'${ans[0]}','${ans[1]}','${ans[2]}'`; // what am I ensuring here? what is being implied and why?
    }

    // State variable to hold the Typography content
    const [textContent, setTextContent] = useState('Congrats! You found my playground... Enjoy...');

    const handleSubmit = () => {
        const data= {
            letter: document.getElementById('letter').value,
            number: document.getElementById('number').value,
            array: document.getElementById('array').value,
        };

        axios.post('http://localhost:3001/resolve', data)
            .then(response => {
                if (response.data.responseText != '') {
                    setTextContent(response.data.responseText);
                }
                else {
                    console.log(data.array.length)
                    if (data.array.length === 41)   {
                        setTextContent(`[${shuffle(data.array)}]`);
                    } else {
                        setTextContent(`the string size is incorrect. Make sure it is a 5 element array of strings of length 5. Also make sure strings are in single quotes in this format: ['abcde','fghij','klmno','pqrst','uvwxy']`)
                    }
                }
            })
            .catch(error => {
                console.error('Error sending message', error);
            });
    };

    return (
        <div style={{ marginTop: '210px', alignItems: 'center', flexDirection: 'column', display: 'flex', }} justifycontent='center'>
            <Card variant="outlined" color="primary" style={{ backgroundColor: '#E5E5E5', boxShadow: '5px 5px 5px rgba(0,0,0, 0.99)', }} sx={{ Height: 250, minWidth: 450, maxWidth: 450 }}>
                <Stack spacing={2} alignItems="center">
                    <Typography> Rules: You may submit ANY of three ways, Letter + Number, Array, and Letter + Number + Array. All types of submissions will be needed at some point.</Typography>
                    <Stack direction='row' spacing={7.5} alignItems="center" justifyContent="space-between">
                        <TextField id="letter" label="Letter" variant="outlined" type='text' maxRows={1} />
                        <TextField id="number" label="Number (Left to Right)" variant="outlined" type='text' maxRows={1} />
                    </Stack>
                    <TextField id="array" label="Array" variant="outlined" type='email' fullWidth={true} maxRows={1} />
                    <TextField id="result" label="output" variant='outlined' type = 'text' fullWidth={true} 
                        value={textContent} InputProps={{readOnly: true,}}/>
                    <Button variant="outlined" onClick={handleSubmit}>
                        Submit
                    </Button>
                </Stack>
            </Card>
        </div>
    );
}

export default Playground;